"use client";

import { useState } from "react";
import {
  FaUsers,
  FaCut,
  FaCashRegister,
  FaUserShield,
  FaHistory,
} from "react-icons/fa";
import SidebarAdmin from "@/components/admin/sidebar";

export default function AdminDashboard() {
  const [sidebarOpen, setSidebarOpen] = useState(true);

  // Data statis sementara
  const dashboardData = {
    totalAdmin: 3,
    totalCapster: 8,
    totalUser: 1247,
    totalLayanan: 12,
    totalReservasi: 2456,
  };

  return (
    <div className="min-h-screen bg-gray-50 flex">
      {/* Sidebar Component */}
      <SidebarAdmin sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />

      {/* Main Content */}
      <main className="flex-1 p-8 overflow-auto">
        <div className="max-w-7xl mx-auto">
          {/* Page Header */}
          <div className="mb-8">
            <h1 className="text-3xl font-bold text-gray-900 mb-2">
              Dashboard Admin
            </h1>
            <p className="text-gray-600">
              Selamat datang di panel admin Brocode Barbershop
            </p>
          </div>

          {/* Dashboard Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-8">
            {/* Total Admin */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600 uppercase tracking-wide">
                    Total Admin
                  </p>
                  <p className="text-3xl font-bold text-gray-900 mt-2">
                    {dashboardData.totalAdmin}
                  </p>
                </div>
                <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center">
                  <FaUserShield className="text-blue-600" size={24} />
                </div>
              </div>
            </div>

            {/* Total Capster */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600 uppercase tracking-wide">
                    Total Capster
                  </p>
                  <p className="text-3xl font-bold text-gray-900 mt-2">
                    {dashboardData.totalCapster}
                  </p>
                </div>
                <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
                  <FaCut className="text-green-600" size={24} />
                </div>
              </div>
            </div>

            {/* Total User */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600 uppercase tracking-wide">
                    Total User
                  </p>
                  <p className="text-3xl font-bold text-gray-900 mt-2">
                    {dashboardData.totalUser.toLocaleString()}
                  </p>
                </div>
                <div className="w-12 h-12 bg-purple-100 rounded-xl flex items-center justify-center">
                  <FaUsers className="text-purple-600" size={24} />
                </div>
              </div>
            </div>

            {/* Total Layanan */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600 uppercase tracking-wide">
                    Total Layanan
                  </p>
                  <p className="text-3xl font-bold text-gray-900 mt-2">
                    {dashboardData.totalLayanan}
                  </p>
                </div>
                <div className="w-12 h-12 bg-orange-100 rounded-xl flex items-center justify-center">
                  <FaCashRegister className="text-orange-600" size={24} />
                </div>
              </div>
            </div>

            {/* Total Reservasi */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600 uppercase tracking-wide">
                    Total Reservasi
                  </p>
                  <p className="text-3xl font-bold text-gray-900 mt-2">
                    {dashboardData.totalReservasi.toLocaleString()}
                  </p>
                </div>
                <div className="w-12 h-12 bg-yellow-100 rounded-xl flex items-center justify-center">
                  <FaHistory className="text-yellow-600" size={24} />
                </div>
              </div>
            </div>
          </div>

          {/* Additional Content Area */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Recent Activities */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <h2 className="text-xl font-bold text-gray-900 mb-6">
                Aktivitas Terbaru
              </h2>
              <div className="space-y-4">
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 bg-green-100 rounded-full flex items-center justify-center">
                    <FaUsers className="text-green-600" size={16} />
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">
                      User baru mendaftar
                    </p>
                    <p className="text-sm text-gray-500">5 menit lalu</p>
                  </div>
                </div>
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
                    <FaHistory className="text-blue-600" size={16} />
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">
                      Reservasi baru dibuat
                    </p>
                    <p className="text-sm text-gray-500">15 menit lalu</p>
                  </div>
                </div>
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 bg-purple-100 rounded-full flex items-center justify-center">
                    <FaCut className="text-purple-600" size={16} />
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">
                      Layanan baru ditambahkan
                    </p>
                    <p className="text-sm text-gray-500">1 jam lalu</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Quick Stats */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100">
              <h2 className="text-xl font-bold text-gray-900 mb-6">
                Statistik Hari Ini
              </h2>
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Reservasi Hari Ini</span>
                  <span className="font-bold text-2xl text-gray-900">23</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Pendapatan Hari Ini</span>
                  <span className="font-bold text-2xl text-gray-900">
                    Rp 1,450,000
                  </span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Capster Aktif</span>
                  <span className="font-bold text-2xl text-gray-900">6/8</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">User Online</span>
                  <span className="font-bold text-2xl text-gray-900">124</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
